import { useEffect, useState } from "react";
import {
  connectWallet,
  getCurrentWalletConnected, getmaxSupply,
  getNumberOfMintedNfts,
  mintNFT,
} from "./util/interact.js";
import logo from './images/logo.png';
import picture from './images/351.png';
import './Minter.css';
import Status from './Status';

const contractABI = require('./contractAbi.json')
const { createAlchemyWeb3 } = require("@alch/alchemy-web3");
const web3 = createAlchemyWeb3(process.env.REACT_APP_ALCHEMY_KEY);

const Minter = () => {
  const [walletAddress, setWallet] = useState("");
  
  // INSTALL_METAMASK, CONNECT_TO_WALLET, MINTING_READY
  const [status, setStatus] = useState("INSTALL_METAMASK");
  const [error, setError] = useState("");
  const [isMintingEnabled, setMintingEnabled] = useState(null);
  const [txHash, setTxHash] = useState('');
  const [numberOfMintedNfts, setNumberOfMintedNfts] = useState();
  const [supply, setSupply] = useState();

  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [url, setURL] = useState("");

  useEffect(async () => {
    const n = await getNumberOfMintedNfts();
    setNumberOfMintedNfts(n);
    const n2 = await getmaxSupply();
    setSupply(n2)

  }, [])

  useEffect(async () => {

    window.contract = await new web3.eth.Contract(contractABI, process.env.REACT_APP_CONTRACT_ADDRESS);
    let mintEnabled = await window.contract.methods.mintingEnabled().call();
    setMintingEnabled(mintEnabled);

    
    if (window.ethereum && mintEnabled) {

      setStatus('CONNECT_TO_WALLET');
      // Get current wallet
      try {
        const addressArray = await window.ethereum.request({
          method: "eth_accounts",
        });

        if (addressArray.length > 0) {
          setWallet(addressArray[0]);
          setStatus('MINTING_READY');
        }
      } catch (err) {
        setError(err);
        return;
      }
    }

    addWalletListener();
  }, []);

  function addWalletListener() {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);
          setStatus("MINTING_READY");
        }
      });
    }
  }

  const connectWalletPressed = async () => {
    const wallet = await connectWallet();

    setStatus(wallet != '' ? 'MINTING_READY' : 'CONNECT_TO_WALLET')
    setWallet(wallet);
  };

  const onMintPressed = async () => {
    const hash = await mintNFT(url, name, description);

    if (hash != '') {
      setName("");
      setDescription("");
      setURL("");
      setTxHash(hash);
    }
  };

  if (isMintingEnabled === null) {
    return <></>;
  }

  if (!isMintingEnabled) {
    return <div>Minting is paused, please come back later...</div>;
  }

  return (
    <div className="Minter">

      <div className="intro">
        <img className="logo" src={logo} alt="SpareBank1 logo" />
        <h1>NFT Minter</h1>
        <svg className="scroll-down-arrow" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
          <path d="M374.6 310.6l-160 160C208.4 476.9 200.2 480 192 480s-16.38-3.125-22.62-9.375l-160-160c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 370.8V64c0-17.69 14.33-31.1 31.1-31.1S224 46.31 224 64v306.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0S387.1 298.1 374.6 310.6z"/>
        </svg>
      </div>

      <Status status={status} onConnectToMetaMaskClick={connectWalletPressed}/>

      {status === 'MINTING_READY' &&
        <div className="mint-container">
          <img className="mint-example" src={picture} alt="Eksempelbilde" />
          {
            walletAddress.length > 0 && <span>Wallet: {walletAddress.substring(0, 6) + "..." + walletAddress.substring(38)}</span>
          }
          <button id="mintButton" onClick={onMintPressed}>
            Mint NFT
          </button>
        </div>
      }

      {txHash !== '' && 
        <p>Grattis, du kan sjekke ut <a href={`https://opensea.io/account`}>din nye NFT p√• Opensea</a></p>
      }

      {numberOfMintedNfts && 
        <p>Supply: {numberOfMintedNfts} / {supply}</p>
      }
    </div>
  );
};

export default Minter;
